<template>
	<div class="cont-wap">
		<div class="my-nav">
			<div>
				<span class="myl">联系人</span>
				<span class="myr">
					<router-link to="/mainSearch"><img src="../../../static/img/icon/search.png" alt=""></router-link>
					<a @click="showAddMenue" href="##"><img src="../../../static/img/icon/add.png" alt=""></a>
				</span>
			</div>
		</div>
		<div class="contact-mainblock">
			<div class="conlist" style="border-top:0px;">
				<router-link to="/mainContacts/cfriend" class="items clearfix">
					<i class="item-img fl"><img src="../../../static/img/icon/friend.png" alt=""></i>
					<span class="item-span fl"><em>钉钉好友</em></span>
				</router-link>
				<router-link to="/mainContacts/telephone" class="items clearfix">
					<i class="item-img fl"><img src="../../../static/img/icon/phone1.png" alt=""></i>
					<span class="item-span fl"><em>手机通讯录</em></span>
				</router-link>
				<router-link to="/mainContacts/mygroup" class="items clearfix">
					<i class="item-img fl"><img src="../../../static/img/icon/group.png" alt=""></i>
					<span class="item-span fl"><em>我的群组</em></span>
				</router-link>
				<router-link to="/mainContacts/service" class="items clearfix">
					<i class="item-img fl"><img src="../../../static/img/icon/service1.png" alt=""></i>
					<span class="item-span fl" style="border-bottom:0;"><em>服务窗</em></span>
				</router-link>
			</div>
			<div class="conlist">
				<div class="items clearfix" @click="collapase()">
					<i class="item-img fl"><img src="../../../static/img/icon/jiagou.png" alt=""></i>
					<span class="item-span fl" style="border-bottom:0;"><em>我的企业团队</em></span>
				</div>
				<div class="hide" v-show="willShow">
					<router-link to="/mainContacts/organize" class="items clearfix">
						<i class="item-img fl"><img src="../../../static/img/icon/zhejiao.png" alt=""></i>
						<span class="item-span fl"><em>组织架构</em></span>
					</router-link>
					<router-link to="/mainContacts/outContact" class="items clearfix">
						<i class="item-img fl"><img src="../../../static/img/icon/zhejiao.png" alt=""></i>
						<span class="item-span fl"><em>外部联系人</em></span>
					</router-link>
				</div>
			</div>
			<div class="conlist">
				<div class="items clearfix"  @click="collapase1()">
					<i class="item-img fl"><img src="../../../static/img/icon/jiagou.png" alt=""></i>
					<span class="item-span fl" style="border-bottom:0;"><em>深圳市智慧建设科技有限公司</em></span>
				</div>
				<div class="hide" v-show="willHide">
					<router-link to="/mainContacts/organize" class="items clearfix">
						<i class="item-img fl"><img src="../../../static/img/icon/zhejiao.png" alt=""></i>
						<span class="item-span fl"><em>组织架构</em></span>
					</router-link>
					<router-link to="/mainContacts/outContact" class="items clearfix">
						<i class="item-img fl"><img src="../../../static/img/icon/zhejiao.png" alt=""></i>
						<span class="item-span fl"><em>外部联系人</em></span>
					</router-link>
				</div>
			</div>
			<div class="conlist">
				<router-link to="/cont/creatTeam" class="items clearfix">
					<i class="item-img fl"><img src="../../../static/img/icon/add.png" alt=""></i>
					<span class="item-span fl" style="border-bottom:0;"><em>创建团队</em></span>
				</router-link>
			</div>
		</div>
		<add-menue ref="addMenue" ></add-menue >

	</div>
</template>
<script>
	import addMenue from './moreMenue'
	export default{
		name: 'd-msg',
		data:function(){
			return{
				willShow:true,
				willHide:true
			}
		},
		components:{
			addMenue
		},
		methods:{
			showAddMenue(){
				this.$refs.addMenue.menue()
			},
			collapase:function(){
				 this.willShow= !this.willShow
			},
			collapase1:function(){
		    this.willHide=!this.willHide
			}
		}
	}
</script>
<style lang='less' scoped>
	.fl{float:left;}
	.fr{float:right;}
	.clearfix:after{
		content:"";
		clear:both;
		height:0;
		visibility: hidden;
		display:block;
	}
		.cont-wap{
			background-color:#EFEFF4;
			height:100%;
			width:100%;
			font-size: 16px;
			.my-nav{
				height:55px;
				width:100%;
				background-color:#fff;
				padding:10px 0;
				position:fixed;
				top:0;
				left:0;
				z-index: 333;
				border-bottom:1px solid #EBEBEB;
				div{
					position:relative;
					height:100%;
					.myl{
						position:absolute;
						left:2rem;
						font-size:20px;
						color:#000;
					}
					.myr{
						display:block;
						position:absolute;
						right:1rem;
						a{
							margin-left:1rem;
						}
					}
				}
			}
			.contact-mainblock{
				margin:55px 0;
				.conlist{
					width:100%;
					background-color:#fff;
					border-top:1px solid #E4E4E7;
					border-bottom:1px solid #E4E4E7;
					margin-bottom:1rem;
					.items{
						display: block;
						height:2.4rem;
						.item-img{
							display:block;
							height:2.4rem;
							line-height: 2.4rem;
							width:20%;
							position:relative;
							img{
								width: 32px;
								display:block;
								position:absolute;
								top:50%;
								margin-top:-16px;
								left:50%;
								margin-left:-16px;
							}
						}
						.item-span{
							display:block;
							width:80%;
							height:2.4rem;
							line-height: 2.4rem;
							// position:relative;
							border-bottom:1px solid #DBDBDC;
							em{
								font-style: normal;
								font-size:16px;
								color:#333;
							}
						}

					}
					.hide{
						.items{
							span{
								border-top:1px solid #DBDBDC;
								border-bottom:0;
							}
						}
					}
				}
			}
		}
</style>



