<template>
  <div  v-show="menShow" class="menue">
    <ul>
      <li v-for="item in menueArr" @click="jump(item.path)">
        <img :src="item.imgSrc" alt="">{{item.text}}
      </li>
    </ul>
  </div>
</template>

<script>
  export default {
    name:"addMenue",
    // props:["menueArr"],
    data(){
      return{
        menShow:false,
        menueArr:[
          {
            text:"扫一扫",
            imgSrc:require("../../../static/img/icon/home.png"),
            path:'/saoma'
          },
          {
            text:"发起群聊",
            imgSrc:require("../../../static/img/icon/home.png"),
            path:'/groupChat'
          },
          {
            text:"添加好友",
            imgSrc:require("../../../static/img/icon/home.png"),
            path:'/saoma'
          }
        ]
      }
    },
    mounted(){
      var _this=this;
      $(".menue").click(function(e){
        if(e.target.nodeName=="DIV"){
          _this.menue();
        }
      });
    },
    methods:{
      menue(){
        this.menShow=!this.menShow;
      },
      jump(url){
        this.$router.push({
          path:url
        });
      }
    }
  }
</script>

<style lang="less" scoped>
  @import '../../public/comment.less';
  .menue{
    background:rgba(255,255,255,0);
    position:fixed;
    top:55px;
    left:0;
    z-index: 120;
    width: 100%;
    height:100%;
    >ul{
      background-color:#fff;
      width:50%;
      border:2px solid #E4E4E7;
      box-shadow: 0px 0px 10px #CCCCCC;
      float:right;
      margin-right:.5rem;
      >li{
        color:#000;
        letter-spacing: 1px;
        font-size:16px;
        padding:.8rem;
        >img{
          margin-right:1rem;
        }
      }
    }
  }
</style>



