<template>
  <div class="main">
    <div class="compent"><component :is="currentView"></component></div>
    <div class="footer">
      <div class="footerNav" @click="pageChange($event,'mainMsg')">
        <p><i class="icon-comment-alt"></i></p>
  			<p>消息</p>
      </div>
      <div class="footerNav active" @click="pageChange($event,'mainWork')">
        <p><i class="icon-th-large"></i></p>
  			<p>工作</p>
      </div>
      <div class="footerNav" @click="pageChange($event,'mainContacts')">
        <p><i class="icon-group"></i></p>
  			<p>联系人</p>
      </div>
      <div class="footerNav" @click="pageChange($event,'mainMy')">
        <p><i class="icon-user"></i></p>
  			<p>我的</p>
      </div>
    </div>
  </div>
</template>
<script>
  import router from '../../router'
  import mainWork from './work'
  import mainMsg from './msg'
  import mainContacts from './contacts'
  import mainMy from './my'
	export default{
		name: 'd-footer',
    data:function(){
        return {
            currentView:mainWork,
        }
    },
    components:{
      mainWork,
      mainMsg,
      mainContacts,
      mainMy
    },
    methods:{
      pageChange:function(event,url){
        this.currentView=url;
        $(event.currentTarget).addClass("active").siblings(".active").removeClass("active");
      }
    }
	}

</script>
<style lang='less' scoped>
  .compent{
    padding-bottom: 50px;
  }
	.footer{
		position:fixed;
    height:60px;
		bottom:0;
		left:0;
		width:100%;
		z-index:333;
		border-top:1px solid #E3E3E3;
		display:flex;
    justify-content: center;
    align-items:center;
    align-content:center;
		background:#fff;
		.footerNav{
			flex:1;
			text-align: center;
			vertical-align: middle;
			padding-bottom:8px;
			color:#7D8790;
			text-decoration:none;
			p{
			  margin-top:5px;
        >i{
          font-size: 18px;
        }
				&:nth-of-type(2){
				  margin-top:7px;
					font-size:12px;
          height: 14px;
				}
			}
		}
		.footerNav.active{
	      color:#38ADFF !important;
    		text-decoration:none !important;
	    }

	}

</style>



