<template>
	<div class="cont-wap">
		<div class="msg-wap">
			<div class="workHead">
				<span>深圳市某某科技有限公司</span>
				<div class="fr">
					<a href="javascript:void(0)" @click="jump('/notice')"><span>6</span><img src="../../../static/img/tishi.png" alt=""></a>
					<router-link to="/mainSearch"><img src="../../../static/img/search.png" alt=""></router-link>
					<a @click="showAddMenue()" href="javascript:void(0)"><img src="../../../static/img/jia.png" alt=""></a>
				</div>
			</div>
			<ul v-show="msgShowSta" class="msg-content">
				<router-link to="/msgNewFriend">
					<li>
						<div class="msg-list-log">
							<span class="dingNewFriend"><i class="icon iconfont icon-tianjiahaoyou"></i></span>
						</div>
						<div class="msg-list-cont">
							<p><span>新的好友</span><span>昨天</span></p>
							<p>这里是相关信息</p>
						</div>
					</li>
				</router-link>

				<li>
					<div class="msg-list-log">
						<span class="dingFile"><i class="icon iconfont icon-tianjiahaoyou"></i></span>
					</div>
					<div class="msg-list-cont">
						<p @click="openCamera"><span>文件小助手</span><span></span></p>
						<p>这里是相关信息</p>
					</div>
				</li>
				<li>
					<div class="msg-list-log">
						<span class="dingPhone"><i class="icon iconfont icon-dingdingdianhua"></i></span>
					</div>
					<div class="msg-list-cont">
						<p @click="getScreen"><span>钉钉电话</span><span>昨天</span></p>
						<p>这里是相关信息</p>
					</div>
				</li>
        <router-link to="/dingMi">
                    <li>
                        <div class="msg-list-log">
                            <span class="dingNewFriend"><i class="icon iconfont icon-tianjiahaoyou"></i></span>
                        </div>
                        <div class="msg-list-cont">
                            <p><span>钉钉小秘书</span><span>昨天</span></p>
                            <p>{{ introduce }}</p>
                        </div>
                    </li>
        </router-link>
        <li @click="jump('/notice')">
          <div class="msg-list-log">
            <span class="dingPhone"><i class="icon iconfont icon-dingdingdianhua"></i></span>
          </div>
          <div class="msg-list-cont">
            <p><span>工作通知</span><span>17:49</span></p>
            <p>恭喜你已开通了钉邮</p>
          </div>
        </li>
			</ul>
			<ul class="msg-content chat">
				<li v-for="item in groupList" @click="jump('/groupChatPage',item)">
					<div class="msg-list-log">
						<img v-if="item.imgSrc" :src="item.imgSrc" alt="">
						<img v-else src="../../../static/img/icon/group.png" alt="">
					</div>
					<div class="msg-list-cont">
						<p><span>{{item.name}}</span><span></span></p>
						<p>欢迎{{item.memberList[item.memberList.length-1].name}}加入本群</p>
					</div>
				</li>
			</ul>

		</div>
		<add-menue ref="addMenue"></add-menue >
	</div>
</template>
<script>
	import addMenue from './moreMenue'
	import {mapState,mapMutations} from "vuex"
	export default{
		name: 'd-msg',
        data: function(){
            return {
                introduce: "钉钉3.4版本新功能介绍",
								msgShowSta:true
            }
        },
				computed:{
					...mapState(["groupList"])
				},
				components:{
					addMenue
				},
        methods: {
					showAddMenue(){
						this.$refs.addMenue.menue()
					},
					...mapMutations(["updateGroupMsg"]),
					jump(url,item){
						if(item){this.updateGroupMsg(item)}
						this.$router.push({path:url})
					},
            openCamera: function(){
                summer.openCamera({
                    callback : function(args){
                        $summer.alert(args);
                        alert(args.imgPath);
                    }
                })
            },
            getScreen: function(){
                summer.openScanner({
                    callback : function (args){
                        $summer.alert(args)
                    }
                })
            }
        }
	}
</script>
<style  lang='less' scoped>
    .cont-wap{
        width:100%;
        height:100%;
        background-color:#EFEFF4;
        .msg-wap{
						.workHead{
							height: 55px;
							width:100%;
							display:flex;
							align-items:center;
							overflow: hidden;
							zoom: 1;
							position:fixed;
							top:0;
							left:0;
							z-index:900;
							background:#fff;
							border-bottom:1px solid #E4E4E4;
							>span{
								float:left;
								width:50%;
								height:55px;
								line-height:55px;
								padding-left:20px;
								box-sizing:border-box;
								white-space:nowrap;
								text-overflow:ellipsis;
								overflow:hidden;
								margin-right:50px;
								color:#666666;
								font-size:18px;
							}
							div.fr{
								float: right;
								display: inline-block;
								width: 50%;
								line-height: 55px;
								padding-right: .5rem;
								text-align: right;
								a{
									display: inline-block;
									line-height: 100%;
									position: relative;
									height:100%;
									&:nth-of-type(2){
										margin:0 1rem;
									}
									img{
										width:21px;
										height:auto;
									}
									>span{
										position: absolute;
										right: -2px;
										top:-10px;
										color:red;
										font-size: 10px;
									}
								}
							}
						}
            .msg-content{
                width:100%;
								margin-top: 55px;
                padding-left:1rem;
                background-color:#fff;
                box-sizing:border-box;
                li{
                    width:100%;
                    height:3.5rem;
                    background-color:#fff;
                    display:flex;
                    .msg-list-log,.msg-list-cont{
                        float:left;
                    }
                    .msg-list-log{
                        width:3.9rem;
                        height:100%;
                        display:flex;
                        align-items:center;
                        span{
                            display:inline-block;
                            width:2.8rem;
                            height:2.8rem;
                            border-radius:50%;
                            text-align:center;
                            line-height:3.1rem;
                            background:#16C295;
                            i{
                                font-size:30px;
                                color:#fff;
                            }
                        }
                    }
                    .msg-list-cont{
                        flex:1;
                        padding-top:.4rem;
                        box-sizing:border-box;
                        border-bottom:1px solid #D9D9D9;
                        p{
                            width:100%;
                            &:first-child{
                                font-size:16px;
                                margin-bottom:.4rem;
                                display: flex;
                                align-items: center;
                                span{
                                    display:block;
                                    &:nth-of-type(1){
                                        float: left;
                                        color:#000;
                                        padding:0 .25rem;
                                    }
                                    &:nth-of-type(2){
                                        float: right;
                                        font-size:12px;
                                        color:#B8B8B8;
                                    }
                                }
                            }
                            &:last-child{
                                font-size:14px;
                                color:#888888;
                            }

                        }

                    }
                }
            }
						.chat{
							margin-top:0;
							>li{
								.msg-list-log{
									>img{
										width:55px;
									}
								}
								.msg-list-cont{
									>p{
										&:first-child{
											>span:first-child{
												max-width: 150px;
												overflow: hidden;
												white-space: nowrap;
												text-overflow: ellipsis;
											}
										}
									}
								}
							}
						}
        }
    }

	/* .cont-wap{
        width:100%;
        height:91.5%;
